import {routeComponent} from './routeComponent'

export const routes = [

    {
      path: '/register',
      name: 'register',
      component: routeComponent.registerComponent
    },
    {
      path: '/login',
      name: 'login',
      component: routeComponent.loginComponent
    },
    {
      path: '/pwdlogin',
      name: 'pwdlogin',
      component: routeComponent.pwdloginComponent
    },
    {
      path: '/root',
      name: 'root',
        //当路由可见，该组件会进行懒加载 （也是懒加载）
        //commonJS规范  module.exports导出，require()导入
        //ES6规范 export 导出，import 导入 
      component: routeComponent.rootComponent,
      // component: resolve => require(['../views/Index.vue'], resolve)
      // component: function (resolve) {
      //   return require(['../views/Index.vue'], resolve)
      // }

      // 二级路由组件
      children:[
        {
          path: '/home',
          name: 'home',
          component: routeComponent.homeComponent
        },
        {
          path: '/classify',
          name: 'classify',
          component: routeComponent.classifyComponent
        },
        {
          path: '/shopcart',
          name: 'shopcart',
          component: routeComponent.shopcartComponent
        },
        {
          path: '/mine',
          name: 'mine',
          component: routeComponent.mineComponent
        }
      ]

    },

    {
      path: '/detail/:id',
      name: 'detail',
      component: routeComponent.detailComponent
    },


    //重定向
    {
      path: '*',
      redirect: {
        name: 'home'
      }
    }

  ]