
class RouteComponent {

    rootComponent(resolve){
        return require(['../views/Root.vue'],resolve)
    }

    homeComponent(resolve){
        // return resolve =>require(['../views/Home.vue'],resolve)
        return require(['../views/Home.vue'],resolve)
    }
    classifyComponent(resolve){
        return require(['../views/Classify.vue'],resolve)
    }
    shopcartComponent(resolve){
        return require(['../views/Shopcart.vue'],resolve)
    }
    mineComponent(resolve){
        return require(['../views/Mine.vue'],resolve)
    }

    detailComponent(resolve) {
        return require(['../views/Detail.vue'], resolve);
    }

    registerComponent(resolve) {
        return require(['../views/Register.vue'], resolve);
    }
    loginComponent(resolve) {
        return require(['../views/Login.vue'], resolve);
    }
    pwdloginComponent(resolve) {
        return require(['../views/Pwdlogin.vue'], resolve);
    }
}

export const routeComponent = new RouteComponent();