import Vue from 'vue'
import App from './App.vue'
import router from './router'

// 导入axios (发短信)
import axios from 'axios'

import VueAxios from 'vue-axios'

import VueCookies from 'vue-cookies'

//导入vant
import {
  Notify,
  Toast,
  Tabbar,
  TabbarItem,
  Row,
  Col,
  Search,
  Swipe, SwipeItem, 
  Icon,
  NavBar,
  Cell, CellGroup,
  Tab, Tabs,
  GoodsAction,
  GoodsActionIcon,
  GoodsActionButton,
  Sidebar, SidebarItem,
  SubmitBar,
  Checkbox, CheckboxGroup,
  Stepper,
  SwipeCell,
  Button,
  Sku,
  
} from 'vant'

//基于meta设置rem
import 'lib-flexible/flexible'

Vue.config.productionTip = false

Vue.use(VueAxios, axios)

//设置基础请求路径
axios.defaults.baseURL = 'http://127.0.0.1:10008' 

//设置post请求头
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded'

//携带cookie
axios.defaults.withCredentials = true

// 解决跨域(axios请求拦截器) _  
//添加axios请求拦截器, 该方法在请求之前触发
axios.interceptors.request.use(config => {
  // console.log('config ==> ', config);

  if(config.method === 'post'){
    //进行data参数序列化
    if (config.data) {
      let dataString = '';

      for (let key in config.data) {
        dataString += key + '=' + config.data[key] + '&'
      }
      config.data = dataString.slice(0, -1);
    }
  }
  return config;
},err =>{
  return Promise.reject(err);
})


Vue.use(VueCookies)

//注册vant组件
Vue
.use(Notify)
.use(Toast)
.use(Tabbar)
.use(TabbarItem)
.use(Row)
.use(Col)
.use(Search)
.use(Swipe).use(SwipeItem)
.use(Icon)
.use(NavBar)
.use(Cell).use(CellGroup)
.use(Tab).use(Tabs)
.use(GoodsAction)
.use(GoodsActionIcon)
.use(GoodsActionButton)
.use(Sidebar)
.use(SidebarItem)
.use(SubmitBar)
.use(Checkbox).use(CheckboxGroup)
.use(Stepper)
.use(SwipeCell)
.use(Button)
.use(Sku)

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')